.xdsoft_datetimepicker {
  box-shadow: 0 5px 15px -5px rgba(0, 0, 0, 0.506);
  background: #FFFFFF;
  border-bottom: 1px solid #BBBBBB;
  border-left: 1px solid #CCCCCC;
  border-right: 1px solid #CCCCCC;
  border-top: 1px solid #CCCCCC;
  color: #333333;
  font-family: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  padding: 8px;
  padding-left: 0;
  padding-top: 2px;
  position: absolute;
  z-index: 9999;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: none;
  iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 75px;
    height: 210px;
    background: transparent;
    border: none;
  }
  button {
    border: none !important;
  }
}

/*For IE8 or lower*/

.xdsoft_noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  &::selection, &::-moz-selection {
    background: transparent;
  }
}

.xdsoft_datetimepicker {
  &.xdsoft_inline {
    display: inline-block;
    position: static;
    box-shadow: none;
  }
  * {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }
  .xdsoft_datepicker, .xdsoft_timepicker {
    display: none;
  }
  .xdsoft_datepicker.active, .xdsoft_timepicker.active {
    display: block;
  }
  .xdsoft_datepicker {
    width: 224px;
    float: left;
    margin-left: 8px;
  }
  &.xdsoft_showweeks .xdsoft_datepicker {
    width: 256px;
  }
  .xdsoft_timepicker {
    width: 58px;
    float: left;
    text-align: center;
    margin-left: 8px;
    margin-top: 0;
  }
  .xdsoft_datepicker.active + .xdsoft_timepicker {
    margin-top: 8px;
    margin-bottom: 3px;
  }
  .xdsoft_mounthpicker {
    position: relative;
    text-align: center;
  }
  .xdsoft_label i, .xdsoft_prev, .xdsoft_next, .xdsoft_today_button {
    background-image: url(data:image/png;base64,);
  }
  .xdsoft_label i {
    opacity: 0.5;
    background-position: -92px -19px;
    display: inline-block;
    width: 9px;
    height: 20px;
    vertical-align: middle;
  }
  .xdsoft_prev {
    float: left;
    background-position: -20px 0;
  }
  .xdsoft_today_button {
    float: left;
    background-position: -70px 0;
    margin-left: 5px;
  }
  .xdsoft_next {
    float: right;
    background-position: 0 0;
    background-color: transparent;
    background-repeat: no-repeat;
    border: 0 none currentColor;
    cursor: pointer;
    display: block;
    height: 30px;
    opacity: 0.5;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
    outline: medium none currentColor;
    overflow: hidden;
    padding: 0;
    position: relative;
    text-indent: 100%;
    white-space: nowrap;
    width: 20px;
  }
  .xdsoft_prev, .xdsoft_today_button {
    background-color: transparent;
    background-repeat: no-repeat;
    border: 0 none currentColor;
    cursor: pointer;
    display: block;
    height: 30px;
    opacity: 0.5;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
    outline: medium none currentColor;
    overflow: hidden;
    padding: 0;
    position: relative;
    text-indent: 100%;
    white-space: nowrap;
    width: 20px;
  }
  .xdsoft_timepicker {
    .xdsoft_prev, .xdsoft_next {
      float: none;
      background-position: -40px -15px;
      height: 15px;
      width: 30px;
      display: block;
      margin-left: 14px;
      margin-top: 7px;
    }
    .xdsoft_prev {
      background-position: -40px 0;
      margin-bottom: 7px;
      margin-top: 0;
    }
    .xdsoft_time_box {
      height: 151px;
      overflow: hidden;
      border-bottom: 1px solid #DDDDDD;
      > div > div {
        background: #F5F5F5;
        border-top: 1px solid #DDDDDD;
        color: #666666;
        font-size: 12px;
        text-align: center;
        border-collapse: collapse;
        cursor: pointer;
        border-bottom-width: 0;
        height: 25px;
        line-height: 25px;
        &:first-child {
          border-top-width: 0;
        }
      }
    }
  }
  .xdsoft_today_button:hover, .xdsoft_next:hover, .xdsoft_prev:hover {
    opacity: 1;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
  }
  .xdsoft_label {
    display: inline;
    position: relative;
    z-index: 9999;
    margin: 0;
    padding: 5px 3px;
    font-size: 14px;
    line-height: 20px;
    font-weight: bold;
    background-color: #fff;
    float: left;
    width: 182px;
    text-align: center;
    cursor: pointer;
    &:hover {
      > span {
        text-decoration: underline;
      }
      i {
        opacity: 1.0;
      }
    }
    > .xdsoft_select {
      border: 1px solid #ccc;
      position: absolute;
      right: 0;
      top: 30px;
      z-index: 101;
      display: none;
      background: #fff;
      max-height: 160px;
      overflow-y: hidden;
      &.xdsoft_monthselect {
        right: -7px;
      }
      &.xdsoft_yearselect {
        right: 2px;
      }
      > div > .xdsoft_option {
        &:hover {
          color: #fff;
          background: #ff8000;
        }
        padding: 2px 10px 2px 5px;
        text-decoration: none !important;
        &.xdsoft_current {
          background: #33AAFF;
          box-shadow: #178FE5 0 1px 3px 0 inset;
          color: #fff;
          font-weight: 700;
        }
      }
    }
  }
  .xdsoft_month {
    width: 100px;
    text-align: right;
  }
  .xdsoft_calendar {
    clear: both;
  }
  .xdsoft_year {
    width: 48px;
    margin-left: 5px;
  }
  .xdsoft_calendar {
    table {
      border-collapse: collapse;
      width: 100%;
    }
    td > div {
      padding-right: 5px;
    }
    th {
      height: 25px;
    }
    td, th {
      width: 14.2857142%;
      background: #F5F5F5;
      border: 1px solid #DDDDDD;
      color: #666666;
      font-size: 12px;
      text-align: right;
      vertical-align: middle;
      padding: 0;
      border-collapse: collapse;
      cursor: pointer;
      height: 25px;
    }
  }
  &.xdsoft_showweeks .xdsoft_calendar {
    td, th {
      width: 12.5%;
    }
  }
  .xdsoft_calendar {
    th {
      background: #F1F1F1;
    }
    td {
      &.xdsoft_today {
        color: #33AAFF;
      }
      &.xdsoft_default, &.xdsoft_current {
        background: #33AAFF;
        box-shadow: #178FE5 0 1px 3px 0 inset;
        color: #fff;
        font-weight: 700;
      }
    }
  }
  .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current {
    background: #33AAFF;
    box-shadow: #178FE5 0 1px 3px 0 inset;
    color: #fff;
    font-weight: 700;
  }
  .xdsoft_calendar td {
    &.xdsoft_other_month, &.xdsoft_disabled {
      opacity: 0.5;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
    }
  }
  .xdsoft_time_box > div > div.xdsoft_disabled {
    opacity: 0.5;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=50)";
  }
  .xdsoft_calendar td {
    &.xdsoft_other_month.xdsoft_disabled {
      opacity: 0.2;
      -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=20)";
    }
    &:hover {
      color: #fff !important;
      background: #ff8000 !important;
      box-shadow: none !important;
    }
  }
  .xdsoft_timepicker .xdsoft_time_box > div > div:hover {
    color: #fff !important;
    background: #ff8000 !important;
    box-shadow: none !important;
  }
  .xdsoft_calendar td.xdsoft_disabled:hover, .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_disabled:hover {
    color: inherit	!important;
    background: inherit !important;
    box-shadow: inherit !important;
  }
  .xdsoft_calendar th {
    font-weight: 700;
    text-align: center;
    color: #999;
    cursor: default;
  }
  .xdsoft_copyright {
    color: #ccc !important;
    font-size: 10px;
    clear: both;
    float: none;
    margin-left: 8px;
    a {
      color: #eee !important;
      &:hover {
        color: #aaa !important;
      }
    }
  }
}

.xdsoft_time_box {
  position: relative;
  border: 1px solid #ccc;
}

.xdsoft_scrollbar {
  > .xdsoft_scroller {
    background: #ccc !important;
    height: 20px;
    border-radius: 3px;
  }
  position: absolute;
  width: 7px;
  right: 0;
  top: 0;
  bottom: 0;
  cursor: pointer;
}

.xdsoft_scroller_box {
  position: relative;
}

.xdsoft_datetimepicker.xdsoft_dark {
  box-shadow: 0 5px 15px -5px rgba(255, 255, 255, 0.506);
  background: #000000;
  border-bottom: 1px solid #444444;
  border-left: 1px solid #333333;
  border-right: 1px solid #333333;
  border-top: 1px solid #333333;
  color: #cccccc;
  .xdsoft_timepicker .xdsoft_time_box {
    border-bottom: 1px solid #222222;
    > div > div {
      background: #0a0a0a;
      border-top: 1px solid #222222;
      color: #999999;
    }
  }
  .xdsoft_label {
    background-color: #000;
    > .xdsoft_select {
      border: 1px solid #333;
      background: #000;
      > div > .xdsoft_option {
        &:hover {
          color: #000;
          background: #007fff;
        }
        &.xdsoft_current {
          background: #cc5500;
          box-shadow: #b03e00 0 1px 3px 0 inset;
          color: #000;
        }
      }
    }
    i {
      background-image: url(data:image/png;base64,);
    }
  }
  .xdsoft_prev, .xdsoft_next, .xdsoft_today_button {
    background-image: url(data:image/png;base64,);
  }
  .xdsoft_calendar {
    td {
      background: #0a0a0a;
      border: 1px solid #222222;
      color: #999999;
    }
    th {
      background: #0a0a0a;
      border: 1px solid #222222;
      color: #999999;
      background: #0e0e0e;
    }
    td {
      &.xdsoft_today {
        color: #cc5500;
      }
      &.xdsoft_default, &.xdsoft_current {
        background: #cc5500;
        box-shadow: #b03e00 0 1px 3px 0 inset;
        color: #000;
      }
    }
  }
  .xdsoft_timepicker .xdsoft_time_box > div > div.xdsoft_current {
    background: #cc5500;
    box-shadow: #b03e00 0 1px 3px 0 inset;
    color: #000;
  }
  .xdsoft_calendar td:hover, .xdsoft_timepicker .xdsoft_time_box > div > div:hover {
    color: #000 !important;
    background: #007fff !important;
  }
  .xdsoft_calendar th {
    color: #666;
  }
  .xdsoft_copyright {
    color: #333 !important;
    a {
      color: #111 !important;
      &:hover {
        color: #555 !important;
      }
    }
  }
}

.xdsoft_dark {
  .xdsoft_time_box {
    border: 1px solid #333;
  }
  .xdsoft_scrollbar > .xdsoft_scroller {
    background: #333 !important;
  }
}